  display: none;
}

.Box2 {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  /* top: 10%; */
  /* left: 5%; */
  /* right: 5%; */
  width: 100%;
  height: 100%;
}

.Box h2 {
  color: #ff00c7;
  font-size: 35px;
  font-weight: bolder;
  margin-top: 0rem;
  line-height: 32px;
  font-family: 'Karma Medium';
  display: flex;
  justify-content: center;
}

.Box p {
  width: 70%;
  margin: 0 auto 0 auto;
  font-size: 18px;
}

.submit {
  display: block;
  width: 40%;
  height: 40px;
  border-color: transparent;
  border-radius: 6px;
  font-size: 20px;
  font-family:'Karma Medium';
  font-weight: 700;
  cursor: pointer;
}

